.block--sk4.sk-content-block {
  padding-top: 100px;
  padding-bottom: 100px;
}
.block--sk4 .single-block .img-hold {
  margin-bottom: 25px;
  margin-left: auto;
  margin-right: auto;
  max-width: 243px;
  width: 100%;
}
.block--sk4 .single-block .equipment-img {
  max-width: 243px;
  width: 100%;
  max-height: 243px;
  height: 100%;
  object-fit: contain;
  transition: transform 0.3s ease-in-out;
}
.block--sk4 .single-block .img-wrapper {
  border-radius: 50%;
  overflow: hidden;
  width: 100%;
}
.block--sk4 .single-block .green-arrow-img-wrapper {
  position: relative;
  left: 2px;
  transition: 0.3s ease-in-out;
}
.block--sk4 .single-block a.equipment-block-btn {
  text-decoration: none;
}
.block--sk4 .single-block:hover .equipment-img {
  transform: scale(1.05);
}
.block--sk4 .single-block:hover a.equipment-block-btn {
  text-decoration: underline;
}
.block--sk4 .single-block:hover .green-arrow-img-wrapper {
  left: 4px;
}
@media screen and (max-width: 576px) {
  .block--sk4 .blaze-next {
    right: -5% !important;
  }
}
@media screen and (max-width: 580px) {
  .block--sk4 .blaze-next {
    right: -5px !important;
  }
}
@media screen and (max-width: 580px) {
  .block--sk4 .blaze-slider.blaze-container {
    padding: 0 15px;
  }
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .block--sk4 .blaze-container.blaze-slider {
    width: 100%;
  }
}
.block--sk4 .gutter {
  margin-top: 0;
}
@media screen and (min-width: 1025px) {
  .block--sk4 .gutter {
    margin-right: -4rem;
    margin-left: -4rem;
  }
}
.block--sk4 .card-col {
  margin-bottom: 48px;
}
.block--sk4 .card-title {
  font-weight: 700;
}
.block--sk4 .mb-table {
  box-shadow: none;
  position: unset;
}
.block--sk4 .mb-table .mt-box-inner,
.block--sk4 .mb-table .fixed-column-inner {
  background-color: transparent;
}
.block--sk4 .mb-table .mt-box-inner .col,
.block--sk4 .mb-table .fixed-column-inner .col {
  height: 200px;
  margin-bottom: 15px;
  text-align: center;
}
.block--sk4 .mb-table .mt-box-inner > div:not(.mt-header):nth-of-type(odd),
.block--sk4 .mb-table .fixed-column-inner > div:not(.mt-header):nth-of-type(odd) {
  background-color: transparent;
}
.block--sk4 .mb-table .mt-box-inner > div:not(.mt-header):nth-of-type(even),
.block--sk4 .mb-table .fixed-column-inner > div:not(.mt-header):nth-of-type(even) {
  background-color: transparent;
}